@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    font-family: "Poppins";
    font-weight: 400, 500, 600, 700;
    background-color: hsl(0, 100%, 74%);
    background-image: url(/reactjsassignment/static/media/bg-intro-desktop.059183ee.png);    
}

.container{
    display:grid ;
    margin-top: 300px;
    max-width: 1000px;
    margin-left: 700px;
    /* justify-content: center; */
    

    

}

.text{
    width: 70%;
    grid-column: 2/3;
    grid-row: 2/3;
    /* grid-area: main; */
}

.text h1{
    font-size: 45px;
}

.try{
    width: 110%;
    /* height: fit-content; */
    background-color:hsl(249, 43%, 39%) ;
    color: white;
    border-radius: 15px;
    text-align: center;
    grid-column: 3;
     
}
.info{
    
    text-align: center;
    width: 100%;
    grid-area: info;
    background-color: white;
    border-radius: 15px;
    padding: 20px 20px;
    grid-column: 3;
    
}

input[type=text],input[type=email] {
    width: 75%;
    display:inline-block;
    padding: 14px 20px;
    margin: 8px 0;
    border-radius: 10px;
    border: solid hsl(246, 25%, 77%);
    border-width: .5px;
    
    
}

input[type=submit] {
    width: 84%;
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }



footer p{
    font-size: 10px;
    color: hsl(246, 25%, 77%); ;   
}

footer a{
    color: orange;
}

.text h1, p{
    color:white;

}

@media  only screen and (max-width: 600px) {

    
    .container{
        display:block;
        /* flex-direction: column; */
        /* align-items: center; */
        margin-top: 0;
        margin-left: 0;
        text-align: center;
        border: none;
        -webkit-justify-content: center;
                justify-content: center;


        
        
        
        
    }


    .text h1{
        font-size: 30px;
        
    }

    .try{
        width: 50%;
        padding: 10px;
        margin-left: 10px;
    }

    .info{
        width: 50%;
    }

    .text{
        width: 50%;
        margin-left: 10px;

    }



}
